@keyframes adShow {
    100% {
        box-shadow: 0 0 0 0 white;
        position: absolute;
        left: 0;
        top: 0;
        opacity: 1;
        transition: 0s;
    }
}
.tabs {
    &__links {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 8px;
        margin-bottom: 20px;

        & button {
            display: block;
            text-align: center;
            font-weight: 700;
            background: transparent;
            border-radius: 8px;
            padding: 2px;

            & p {
                display: flex;
                align-items: center;
                justify-content: center;
                background: #e9efff;
                border-radius: 6px;
                padding: 10px;
                height: 100%;
            }
        }

        &.pink {
            & button {
                background: linear-gradient(180deg, #ff007a 0%, #9c00c3 100%);
                & p {
                    color: #4630ba;
                    font-size: 8px;
                }

                &.active {
                    & p {
                        color: #fff;
                        background: transparent;
                    }
                }
            }
        }
        &.purple {
            & button {
                background: linear-gradient(0deg, #1a0b4e 0%, #4630ba 100%);
                & p {
                    color: #4630ba;
                    font-size: 8px;
                }

                &.active {
                    & p {
                        color: #fff;
                        background: transparent;
                    }
                }
            }
        }
    }

    .tab-content {
        h2 {
            font-weight: 700;
            font-size: 14px;
            text-transform: uppercase;
            color: #1a2b63;
            margin-bottom: 8px;
        }
        & > img {
            display: block;
            margin: 0 auto 20px;
        }
    }
}
.tabs-loan {
    &__header {
        margin-bottom: 20px;
        p {
            font-weight: 700;
            font-size: 14px;
            color: #1a2b63;
            max-width: 240px;
        }
    }
}
.sponsor-tabs {
    & > .tabs > .tabs__links {
        & button:nth-child(2) {
            position: relative;
            &::after {
                content: "";
                display: block;
                position: absolute;
                left: 0;
                top: 0;
                width: 100%;
                height: 100%;
                opacity: 0;
                transition: all 0.5s;
                box-shadow: 0 0 10px 40px #4630ba;
                animation: adShow 600ms ease-in;
            }
        }
    }
}
