.referals {
    display: block;
    width: 100%;
    border-radius: 10px;
    thead {
        padding-bottom: 10px;
        background: #fff;
        margin-bottom: 1px;
        padding: 10px 15px;
        border-radius: 10px 10px 0px 0px;
        font-size: 10px;
    }
    thead,
    tbody tr {
        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: 20px;
    }
    tbody tr {
        padding: 6px 15px;
        background: #fff;
        font-size: 10px;
        &:last-child {
            border-radius: 0px 0px 10px 10px;
        }
    }
    tbody {
        display: flex;
        flex-direction: column;
        gap: 1px;
    }

    thead td,
    tbody td {
        &:first-child {
            flex: 0 0 45px;
            max-width: 45px;
        }
        &:nth-child(2) {
            flex: 0 0 80px;
            max-width: 80px;
            text-align: center;
        }
        &:last-child {
            flex: 0 0 60px;
            max-width: 60px;
            text-align: right;
        }
    }
    &__img {
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 20px;
        background: #3f2aa9;
        max-width: fit-content;
        padding: 4px 6px;
        margin-left: auto;

        & svg {
            display: block;
            margin-right: auto;
            width: 10px;
            height: 10px;
        }
    }
}
.refferals-progress {
    position: relative;
    width: 100%;
    height: 20px;
    margin-top: 50px;

    &__steps {
        position: absolute;
        inset: 0;
        top: -70px;
        z-index: 1;
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-left: 60px;
        & img {
            width: 20px;
            height: 30px;
            object-fit: contain;
        }

        div p:not(:last-child) {
            font-size: 8px;
            text-align: center;
            margin-bottom: 4px;
        }
        & .step {
            position: relative;

            & img {
                width: 40px;
                height: 30px;
            }

            &::after {
                content: "";
                height: 30px;
                width: 1px;
                position: absolute;
                top: 100%;
                left: 50%;
                transform: translate(-50%, 0%);
                background: #000;
            }
        }
    }
    &__bar {
        background: #fff;
        height: 100%;
        border-radius: 20px;
        overflow: hidden;
        position: relative;

        span {
            display: flex;
            align-items: center;
            justify-content: center;
            color: #fff;
            font-size: 10px;
            width: 20%;
            position: absolute;
            top: 0px;
            left: 0px;
            height: 100%;
            font-weight: 700;

            &.red {
                background: rgb(248, 63, 63);
            }
            &.gold {
                background: rgb(255, 153, 0);
            }
            &.green {
                background: rgb(0, 186, 0);
            }
            &.gradient {
                background: linear-gradient(-270deg, #f561a8 0%, #9c00c3 100%);
            }
        }
    }
    & + .btn {
        margin-bottom: 20px;
    }
}
