@keyframes animateTurbo {
    0% {
        background-position: 0 0;
    }
    50% {
        background-position: 1000% 0;
    }
    100% {
        background-position: 0% 0;
    }
}
.connect-wallet {
    display: flex;
    justify-content: space-between;

    & p {
        max-width: 150px;
        color: #fff;
    }
    & p:last-child {
        text-align: right;

        span {
            font-weight: 600;
            color: #1a2b63;
        }
    }
}
.total-coins {
    display: flex;
    gap: 4px;
    align-items: center;
    justify-content: center;
    margin-bottom: 10px;

    & img {
        flex: 0 0 27px;
        aspect-ratio: 1/1;
        margin-right: 8px;
    }

    & p {
        font-weight: 700;
        font-size: 20px;
        color: #fff;

        &.total {
            color: #3f2aa9;
        }
    }
}
.text-label {
    display: flex;
    gap: 10px;
    max-width: fit-content;
    align-items: center;
    border-radius: 16px;
    padding: 8px 12px;
    background: #fff;

    margin: 0 auto 12px;

    p {
        font-weight: 600;
        font-size: 10px;

        color: #1a2b63;
    }
}
.turbo {
    display: flex;
    gap: 8px;

    .count,
    &__count {
        display: flex;
        align-items: center;
        justify-content: center;
        flex: 1 0 5rem;
        border-radius: 8px;
        padding: 8px;
    }
    .count {
        background: #3f2aa9;
        color: #fff;
        font-weight: 600;
        font-size: 20px;
        text-align: center;
    }
    &__count {
        background: transparent;
        color: #3f2aa9;
        position: relative;
        &::before,
        &::after {
            content: "";
            position: absolute;
            inset: 0;
            background: linear-gradient(90deg, #fafafd, #d7cff6, #cbc0f1, #c0b5f7, #cbc0f1, #d7cff6, #fafafd);
            background-size: 1000%;

            z-index: -1;
            animation: animateTurbo 60s linear infinite;
            opacity: 1;
            border-radius: 8px;
        }
        &::before {
            filter: blur(20px);
            transition: opacity 0.5s ease-in-out;
        }

        &.active {
            &::before,
            &::after {
                animation: animateTurbo 30s linear infinite;
                background: linear-gradient(90deg, #fdfafa, #f6cfcf, #f1c0c0, #f7b5b5, #f1c0c0, #f6cfcf, #fdfafa);

                background-size: 1000%;
            }
        }
    }
    &__text {
        cursor: pointer;
        & p:first-child {
            font-weight: 600;
            font-size: 10px;
            text-align: center;
            color: #1a2b63;
        }
        & p:last-child {
            font-weight: 400;
            font-size: 10px;
            text-align: center;
            color: #1a2b63;
        }
    }
}
